  filter(type: string) {
    switch (type) {
      case 'All': {
        this.typeClass = 'All';
        this.premaidShowArray = this.premaidCalcList;
        this.selectCalc = this.premaidShowArray[0];
        break;
      }
      case 'Others': {
        this.typeClass = 'Others';
        this.premaidShowArray = [];
        const self = this;
        this.premaidCalcList.map(d => {
          let set = false;
          for (let f of self.filterTypeList) {
            if (d.industry && d.industry.toLowerCase().includes(f)) {
              set = true;
              break;
            }
          }
          if (!set) {
            self.premaidShowArray.push(d);
          }
        });
        this.selectCalc = this.premaidShowArray[0];
        break;
      }
      default: {
        this.typeClass = type;
        this.premaidShowArray = [];
        this.premaidShowArray = this.premaidCalcList.filter(d => d.industry && d.industry.toLowerCase().includes(type));
        this.selectCalc = this.premaidShowArray[0];
        break;
      }
    }

    // jQuery('.owl-carousel-second').owlCarousel('destroy')
    setTimeout(() => {
      this.setPremadeGIF();
      // this.styleAddedToItem()
    }, 0);
  }

  getFeatureaccess(selector: string): boolean {
    selector = selector.split('-').join('_');
    let flag = <boolean>this._featureAuthService.features.templates[selector];
    return flag;
  }

  getPreviewUrls(temp_type:any){
    this._builderService.getCalcOfATemplate(temp_type).subscribe(data => {
      data.map(ele=>{
        this.previewUrls[ele.template]=ele.url;
      });
    }, err => console.log('Template get error: '));
  }

  ngOnInit() {
     (jQuery('body').width() < 1024)?  this.deviceSize = !this.deviceSize : '';
    //console.log("this._featureAuthService.features.templates",this._featureAuthService.features.templates);
    if (localStorage.getItem('changeTemplate') && localStorage.getItem('changeTemplate') == 'true')
      this.changeTemp = true;
    else
      this.changeTemp = false;

    let cookie = this.cookieService.readCookie('storage');
    let storage = cookie != null ? JSON.parse(cookie) : '';
    if (storage && storage.token) {
      this._membershipService.getPlanSubscription()
        .subscribe(
          (success: any) => {
            let subscription = new Subscriptions(success.currentplan.subscription);
            switch (subscription.status) {
              case 'in_trial':
                let difference;
                this.countdownPromoService.subscription = subscription;
                let trialEnd = moment.unix(success.currentplan.subscription.trial_end);
                this.countdownPromoService.setCountdownTimer(trialEnd);
                if (this.countdownPromoService.trialEnd) {
                  difference = moment.duration(this.countdownPromoService.trialEnd.diff(moment(new Date())));
                }
                // if (difference.asDays() <= 4 && difference.asDays() >= 0) {
                //this.showPromo = true;
                //}
                break;
            }
          }
        );
    }
    this.getSelectTemplate();
    this._featureAuthService.getFeatures().subscribe(f => {
      console.log("Innn templates", f, this._featureAuthService.features);
    });
  }

