  generateFilterList() {
    this.filterTypeList = [];
    const self = this;
    for (let i of this.premaidCalcList) {
      let d = i.industry && i.industry.trim();
      if (d)
        d = d.split(',');
      if (d && d.length > 0) {
        d.map(typ => {
          if (self.filterTypeList.findIndex(f => f == typ.toLowerCase()) == -1) {
            self.filterTypeList.push(typ.trim().toLowerCase());
          }
        });
      }
    }
  }
  selectCalcFun(id) {
    this.selectCalc = this.premaidShowArray.find(d => d._id == id);
    this.setPremadeGIF();
  }
