  selectTemplate(selector: any, $event: any) { // Change Template Support Function For Create Cal
    this.createNewCal(selector, $event);
  }

  createDuplicateCal() { // Create Duplicate Calculator Of Selected Calculator
    if (this.premaidShowArray.length && !this.selectCalc.active) {
      let type = this.selectCalc.template.split('-');
      if (type[type.length - 1] === 'new' || type[type.length - 1] === 'v3') { type.pop(); }//changed by avhishek
      type = type.join('_');
      this._featureAuthService.setSelectedFeature('pre-madeTemplates', type);
      jQuery('.templates').addClass('activegreen limited-label');
      jQuery('#premiumModal').modal('show');
      jQuery('.modal-backdrop').insertAfter('#premiumModal');
    } else if (this.premaidShowArray.length) {
      localStorage.setItem('temp_type', this.getTemplateType(this.selectCalc.type));
      localStorage.setItem('project', 'Duplicate');
      localStorage.setItem('DuplicateId', this.selectCalc.liveApp.app);
      let duplicateURL = this.selectCalc.liveApp.url.split('/');
      localStorage.setItem('DuplicateURL', duplicateURL[duplicateURL.length - 1]);
      localStorage.setItem('premade', 'true');
      localStorage.setItem('temp_name', this.selectCalc.template);
      jQuery('#ideaGen').modal('toggle');
      window.location.href = environment.PROTOCOL + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/builder/';
    }
  }
  getTemplateType(template) {
    switch (template) {
      case 'Outcome Quiz': return 'Recommendation'
      case 'Calculator': return 'Numerical'
      case 'Graded Quiz': return 'Graded'
      case 'Poll': return 'Poll'
      case 'Chatbot': return 'Chatbot'
    }
    return template;
  }

  createNewCal(selector, $event) { //For Create Default Calculator
    if (this._featureAuthService.features.templates[selector.split('-').join('_')]) {
      var button = jQuery('.' + selector + '.init');
      button.html('Initializing');
      button.addClass('loading');
      button.attr('disabled', true);
      localStorage.setItem('temp_name', selector);
      if (!localStorage.getItem('changeTemplate')) {
        localStorage.setItem('project', 'New');
      }
      // call change templateitemtype services
      window.location.href = environment.PROTOCOL + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/builder/';
      //this._router.navigate(['/builder']);
      /*---- Tracking code goes here ----*/
      ga('markettingteam.send', 'event', 'Builder', 'Click', 'UseTemplate');
      // _kmq.push(['record', 'Builder Use Template Click', {'Template':selector}]);
      /*---------------------------------*/
    } else {
      let type = selector.split('-');
      if (type[type.length - 1] === 'new' || type[type.length - 1] === 'v3') { type.pop(); }//changed by avhishek
      type = type.join('_');
      this._featureAuthService.setSelectedFeature('templates', type);
      jQuery('.templates').addClass('activegreen limited-label');
      jQuery('#premiumModal').modal('show');
      jQuery('.modal-backdrop').insertAfter('#premiumModal');
    }
  }

  setDuplicateCalModelDefaultSetting(id) { // Function For Set Default Setting Whenever Modal is Loaded
    this.currentSelectapp = {
      _id: id,
      title: "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s",
      name: 'University cost calculator',
      pages: [{
        bgImage: '../../../../assets/images/left-img.jpg'
      }]
    };
  }

  getAvailableTemplates(): any[] {
    return this.templates.filter((template: any) =>
      template.available && (this.templateArray.indexOf(template.selector) >= 0 || !this.templateArray.length) ? true : false
    );
  }


  getTemplateName(template) {
    switch (template) {
      case 'one-page-card-new': return 'The Chicago';
      case 'sound-cloud-v3': return 'The Londoner';
      case 'template-seven': return 'The Seattle';
      case 'inline-temp-new': return 'The Greek';
      case 'experian': return 'The Tokyo';
      case 'template-five': return 'The Madrid';
      case 'template-six': return 'The Stockholm';
      case 'template-eight': return 'The Venice';
      case 'template-nine': return 'The New York';
    }
  }

  getSlidingImage(template: any) {
    if (template.slidingImage[this.temp_type])
      return template.slidingImage[this.temp_type];
    else
      return template.slidingImage['Numerical'];
  }

  getSlidingClass(template: any) {
    if (this.temp_type === 'Poll' && template.selector === 'one-page-card-new') {
      return 't1-poll-image';
    } else if (this.temp_type === 'Poll' && template.selector === 'template-six') {
      return 't6-poll-image';
    } else {
      return 'temp-img';
    }
  }

  setPremadeGIF() {
    let self: any = this;
    jQuery('#og_pramade_gif').attr('src', '');
    // If Loader Gif Needed then un comment below line and add the source.
    // jQuery('#og_pramade_gif').attr('src', '');
    jQuery('#og_pramade_gif').attr('src', self.selectCalc.media);
  }

