  previewPremade() {
    // this.styleAddedToItem();
    // this.premadePreviewUrl = this.selectCalc.liveApp.url;
    // this.premadeVisible = this.selectCalc.title;
    localStorage.setItem('temp_type', this.getTemplateType(this.selectCalc.type));
    this.previewConfig = {
      duplicate: {
        selectCalc: this.selectCalc,
        url: this.selectCalc.liveApp.url,
        name: this.selectCalc.template
      },
      new: null
    }
    localStorage.setItem('previewConfig', JSON.stringify(this.previewConfig));
    window.open('/layoutPreview', '_blank');
    // this._router.navigate(['/layoutPreview']);
  }

  showSelectPremadePreview(index: number) {
    // this.styleAddedToItem();
    let item = this.premaidShowArray[index];
    this.premadePreviewUrl = item.liveApp.url;
    this.premadeVisible = item.title;
  }

